<template>
	<view>
		<form @submit="submit" v-if="configInfo.small_switch == 0">
			<view class="publish-content">
				<view class="publish-content-li">
					<view class="publish-content-li-title"><text class="publish-content-li-title-xing">*</text>标题</view>
					<textarea name="title" @input="title_change" maxlength="30" class="publish-content-li-text"></textarea>
					<view class="publish-content-li-num">
						<text>{{title_num}}</text>
						<text>/</text>
						<text>30</text>
					</view>
				</view>
				
				<view class="publish-content-li">
					<view class="publish-content-li-title"><text class="publish-content-li-title-xing">*</text>内容</view>
					<textarea name="des" @input="text_change" maxlength="10000" class="publish-content-li-text1"></textarea>
					<view class="publish-content-li-num1">
						<text>{{text_num}}</text>
						<text>/</text>
						<text>10000</text>
					</view>
				</view>
				
				<view class="publish-content-img">
					<block v-for="(item,index) in image_list">
						<view class="publish-content-img-li">
							<image v-if="item.isAdd == 1" :data-index="index" @click="delThumb" class="publish-content-img-li-del" src="/static/images/delete.png"></image>
							<image :data-index="index" @click="uploadThumb" class="publish-content-img-li-img" :src="item.url"></image>
						</view>
						
					</block>
				</view>
				
				<button id="submit" form-type="submit">提交审核</button>
			</view>
		</form>
	</view>
</template>

<script>
	import request from '@/util/request.js';
	const app = getApp();
	export default {
		data() {
			return {
				title_num:0,//标题字数
				text_num:0,//内容字数
				image_list:[{'isAdd':0,'url':'/static/images/add.png'}],//添加图片，最多9张
				margin_left:'margin-left:0',
				configInfo:[]
			}
		},
		onShow(){
			var url = app.globalData.site_url+'/appapi/?s=Home.GetConfig';
			request.requestApi(url,{}).then(res=>{
				this.configInfo = res.data.info[0];
			})
		},
		methods: {
			delThumb(e){ //删除图片
				this.image_list.splice(e.currentTarget.dataset.index, 1);
				if(this.image_list.length==9){
					this.image_list.push({isAdd:0,url:'/static/images/add.png'});
				}
			},
			uploadThumb(e){ //上传图片
				var uid = app.globalData.userInfo.id;
				var token = app.globalData.userInfo.token;
				var index = e.currentTarget.dataset.index;
				var _this = this;
				uni.chooseImage({
				  count: 1,  //最多可以选择的图片总数  
				  success: function (res) {
					//启动上传等待中...  
					uni.showLoading({
					  title: '图片上传中',
					});
					uni.uploadFile({
					  url: app.globalData.site_url+'/appapi/?s=Update.UpdateAvatar',
					  filePath: res.tempFilePaths[0],
					  name: 'file',
					  formData: {
						'filename': 'file',
						uid:uid,
						token:token
					  },
					  success: function (res) {
						  console.log(JSON.parse(res.data));
						  var res = JSON.parse(res.data);
						  if(res.data.code == 700){
							  uni.showToast({
							  	title:res.data.msg,
								icon:'none'
							  })
							  
							  setTimeout(function(){
								  uni.redirectTo({
								  	url:'/pages/login/index'
								  })
							  },1500)
						  }
						  
						  if(res.data.code==0){
							  _this.image_list[index].isAdd = 1;
							  _this.image_list[index].url = res.data.info[0].url;
							  if(_this.image_list.length<9 && _this.image_list[index].url=='/static/images/add.png'){
								  _this.image_list.push({isAdd:0,url:'/static/images/add.png'})
							  }
							  uni.showToast({
							  	title:res.data.msg
							  })
						  }else{
							  uni.showToast({
							  	title:res.data.msg,
								icon:'none'
							  })
						  }
					  }
					})
				  }
				})
			},
			submit(e){ //提交审核
				var uid = app.globalData.userInfo.id;
				var token = app.globalData.userInfo.token;
				var title = e.detail.value.title;
				var des = e.detail.value.des;
				if(title==''){
					uni.showToast({
						title:'请填写标题',
						icon:'none'
					})
					return ;
				}
				if(des==''){
					uni.showToast({
						title:'请填写内容',
						icon:'none'
					})
					return ;
				}
				
				var thumbs = {};
				for(var i=0;i<this.image_list.length;i++){
					if(this.image_list[i].isAdd==1){
						thumbs[i]=this.image_list[i].url;
					}
				}
				
				thumbs = JSON.stringify(thumbs);
				
				var url = app.globalData.site_url+'/appapi/?s=Home.Updatethink';
				request.requestApi(url,{uid:uid,token:token,title:title,content:des,thumbs:thumbs}).then(res=>{
					if(res.data.code==0){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
						
						setTimeout(function(){
							uni.navigateBack({
								delta:1
							})
						},1500)
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
				
				
			},
			title_change(e){ //标题发生变化
				this.title_num = e.detail.cursor;
			},
			text_change(e){ //内容发生变化
				this.text_num = e.detail.cursor;
			},
		}
	}
</script>

<style>
	#submit{
	    margin-top: 90rpx;
	    margin-bottom: 40rpx;
	    padding: 0;
	    width: 30%;
	    border: 0;
	    color: #fff;
	    background: linear-gradient(to bottom, #FF9000, #F64330);
	    border-radius: 68rpx;
	    line-height: 70rpx;
	    font-size: 30rpx;
	}
	.publish-content-img-li-img{
	    width: 130rpx;
	    height: 130rpx;
	}
	.publish-content-img-li-del{
		position: absolute;
	    width: 20rpx;
	    height: 20rpx;
	    right: 0;
	}
	.publish-content-img-li:nth-of-type(1){
		margin-left: 0;
	}
	.publish-content-img-li{
	    position: relative;
	    margin-top: 20rpx;
	    float: left;
		margin-left: 20rpx;
	}
	.publish-content-img{
		clear: both;
		overflow: hidden;
	}
	.publish-content-li-num1{
		position: absolute;
		top: 564rpx;
		right: 30rpx;
		font-size: 26rpx;
		color: #969696;
	}
	.publish-content-li-text1{
		padding: 2%;
		width: 95%;
		border: 1rpx solid #E6E6E6;
		margin-top: 20rpx;
		height: 500rpx;
	}
	.publish-content-li-num{
	    position: absolute;
	    top: 266rpx;
	    right: 30rpx;
	    font-size: 26rpx;
	    color: #969696;
	}
	.publish-content-li-text{
	    padding: 2%;
	    width: 95%;
	    border: 1rpx solid #E6E6E6;
	    margin-top: 20rpx;
	    height: 210rpx;
	}
	.publish-content-li-title-xing{
	    color: #E32A2A;
	}
	.publish-content-li-title{
		margin-top: 24rpx;
	}
	.publish-content{
	    width: 94%;
	    margin: 0 auto;
	}
	.publish-content-li{
	    position: relative;
	    clear: both;
	    overflow: hidden;
	}
</style>
